html {
	box-sizing: border-box;
}
*,
*:before,
*:after {
	box-sizing: inherit;
}

* {
	padding: 0;
	margin: 0;
	position: relative;
}

body {
	touch-action: manipulation;
}

body,
input,
textarea {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", sans-serif;
}

body {
	background: #fdfdfe;
	height: 100vh;
	color: #000;
}

h1 {
	font-weight: 900 !important;
}

:root {
	--accent-color: #eb4d4b;
}
